* {
  margin:0;
  padding:0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.navbar-default {
  background-image: none;
  padding: 5px;
  background-color: #282c33;
  color: #ffffff;
  margin-bottom: 0;
  border-radius: 0;
  border-color: transparent;
  border-bottom: #a37f5c solid 4px;
}

.navbar-default .navbar-header {
  display: flex;
  justify-content: center;
}

.navbar-default .navbar-brand,
.navbar-default .navbar-brand:link,
.navbar-default .navbar-brand:visited {
  color: #f3c765;
}

.navbar-default .navbar-brand:hover,
.navbar-default .navbar-brand:active  {
  color: #a37f5c;
}

.navbar-default .navbar-nav>li>a,
.navbar-default .navbar-nav>li>a:link,
.navbar-default .navbar-nav>li>a:visited {
  color: #ffffff;
}

.navbar-default .navbar-nav>li>a:hover,
.navbar-default .navbar-nav>li>a:active {
  color: #a37f5c;
}

.navbar-default .navbar-nav>.active>a,
.navbar-default .navbar-nav>.active>a:visited {
  background-image: none;
  background-color: #282c33;
  color: #f3c765;
}

.navbar-default .navbar-nav>.active>a:link {
  background-image: none;
  background-color: #282c33;
  color: #f3c765;
}

.navbar-default .navbar-nav>.active>a:hover,
.navbar-default .navbar-nav>.active>a:active {
  background-image: none;
  background-color: #282c33;
  color: #f3c765;
}

.Home-body {
  flex:1 1;
  overflow: auto;
  background-color: #ffffff;
  color: #282c33;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.Home-footer {
  padding: 5px;
  background-color: #282c33;
  color: #f3c765;
}

/*
#212121 // Ebony Clay
#e5e6ea // Mercury
#828592 // Olso Gray
*/
.Links {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.Link-icon-image {
  padding: 5px;
  height: 40px;
  width: 40px;
}

.Link-icon-image:hover {
  opacity: 0.5;
}
.Projects {
  display: flex;
  width: 80vw;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: flex-start;
}

.ProjectsCategory {
  min-width: 30vw;
  max-width: 30vw;
  border: 1px solid #a37f5c;
  border-radius: 10px;
  width: 100%;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.LoadingSymbol {
  padding: 10px;
  height: 20vw;
  width: 20vw;
}

@media(max-width: 800px) {
  .Projects {
    flex-direction: column;
    justify-content: space-evenly;
  }
  .ProjectsCategory {
    min-width: 80vw;
    max-width: 80vw;
  }
}
.Project-main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-width: 1px;
  border-color: #a37f5c;
  border-radius: 10px;
  border-style: solid;
  padding: 20px;
  margin: 20px;
}

.Project-title {
  font-size: 1.3em;
  word-wrap: break-word;
  margin: 0px;
}

.Project-description {
  font-size: 16px;
  margin: 0px;
}

.Project-links {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.Project-icon-image {
  padding: 5px;
  height: 30px;
  width: 30px;
}

.Project-icon-image:hover {
  opacity: 0.5;
}
.Contact-main {
  width: 50%;
  margin: 20px;
  padding: 30px;
  border-width: 1px;
  border-color: #a37f5c;
  border-radius: 10px;
  border-style: solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Contact-main p {
  font-size: 2em; 
}

.Contact-form {
  width: 90%;
  margin: 20px;
  padding: 30px;
  border-width: 1px;
  border-color: #a37f5c;
  border-radius: 10px;
  border-style: solid;
}

textarea.form-control {
  height: 20em;
  resize: none;
}

@media(max-width: 800px) {
  .Contact-main {
    width: 80%;
  }
}
